<template>
<div class="login-box">
	<h1>Login form </h1>
	<hr>
    <input v-model="username">
    <input v-model="password">
    <button @click="getUser">aaaa</button>
    <p></p>
    <a href="#"> forgot your password</a>
    <br>
    <a href="#">Don't have an account</a>
</div>

<div>
    <p> Username : {{ user.username }}</p>
    <p> UserID : {{ user.userid }}</p>
    <p> Access Token : {{ user.access_token }}</p>
    <p> User Status : {{ user.user_status }}</p>
</div>
</template>

<script>
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  name: 'App',
  data(){
    return {
      username : "",
      password : ""
    }
  },
  mounted() {
      this.$store.dispatch('user/user_verify')
  },
  computed:{
    ...mapState(["user", "content", "profile"]),
  },
  components: {
  },
  methods:{
    ...mapMutations(['set_user_status']),    
    ...mapActions( ['user_verify']),    
    getUser(){
      this.$store.dispatch('user/user_get', {id: this.username, pw: this.password})
    }
  }
}
</script>

