<template>
    <div class="card">
      <input @change="upload($event, index)" multiple type="file" id="file" class="inputfile" /> 
      <label for="file" class="input-plus">+</label>

      <draggable
          v-model="this.content.file_data[index]"
          animation="100"
          @start="current(id)"
          @end="drag = false"
          item-key="id"
          @change="dragging( this.content.file_data[index], index)"
      >
        <template #item="{ element }">
          <div @click="current(element.order, index, element.id)"
               @dblclick="test(element.id, element.files)">
            <div class="container">
              <img class="image" :src="element.thumbnail" />
              <div class="overlay">My Name is John</div>
            </div>
            <p> ID :{{ element.id }} </p>
            <p> ORDER :{{ element.order }} </p>
          </div>
        </template>
      </draggable>
      <div class="card-body">
        <h5 class="card-title">Card title</h5>
        <p class="card-text">
          This is a wider card with supporting text below as a natural lead-in
          to additional content. This content is a little bit longer.
        </p>
        <p class="card-text">
          <small class="text-muted">Last updated 3 mins ago</small>
        </p>
        <p>
          <button @click="this.$store.dispatch('content/sort_save', {index:index})">Sort Update</button>
        </p>
      </div>
    </div>
</template>

<script>
import draggable from "vuedraggable";
import { mapState, mapActions, mapMutations } from "vuex";

export default {
  name : "CaRd",
  props:{ 
      index: Number,                      // props 선언하여 사용 
  }, 
  data(){
      return{
          data: [],
      }
  },
  computed: {
  ...mapState(["user", "content", "profile"]),
  },
  created() {
    window.addEventListener('beforeunload', this.leaving)
  },
  unmounted(){
    this.$store.dispatch('content/sort_save', {index: this.index})
    console.log("works------------------------")
  },
  components: {
    draggable,
  },
  methods: {
    ...mapMutations(["set_user_status"]),
    ...mapActions(["user_verify"]),

    leaving(){
      this.$store.dispatch('content/sort_save', {index:this.index})
      console.log("works------------------------")
    },
    upload(event, index){
      let file = event.target.files;

      for(var i = 0 ; i < this.content.file_data[index].length ; i++){
        const dataFile = new FormData();
        const file_id = this.content.file_data[index][i].id
        dataFile.append( 'files' , file[i])
        dataFile.append('id' , file_id)
        this.$store.dispatch('content/put_files', {
                                                  id: file_id, 
                                                  data: dataFile, 
                                                  config: this.user.config,
                                                  index : index
                                                  } )
      }
    },
    current(e, index, id){ 
      console.log( "click works")
      e, index, id
    },
    dragging( count, index) { 
      for (var i = 0; i < count.length; i++) {       // 배열 arr의 모든 요소의 인덱스(index)를 출력함. 
        this.content.file_data[index][i].order = i
      }
      // this.$store.dispatch('content/sort_save', {index:this.index})
    },
    test( id, file){
      this.$store.commit('content/set_status_modal', true);
      this.$store.commit('content/set_clicked_image', file);
      this.$store.commit('content/set_clicked_id', id);


    }
  }
}
</script>

