import axios from 'axios'

const BASE_URL = 'http://linst77.pythonanywhere.com/'

export default{


// Authority ------------------------------------------------------
    user: function(id, token){
        return axios.get(BASE_URL + "/user/" + id + "/", token)
    },

    postToken: function( userid, password){
        return axios.post(BASE_URL + "api/token/", {
            "username" : userid,
            "password" : password
        })
    },

    verifyToken: function( token){
        return axios.post(BASE_URL + "api/token/verify/", {
            "token" : token
        })
    },

    registerUser: function( userid, password){
        return axios.post(BASE_URL + "api/register/", {
            "username" : userid,
            "password" : password
        })
    },
    getProfile: function( id, token){
        return axios.get(BASE_URL + "/profile/" + id + "/", token)
    },
// sample for two requests as one ---------------------------------------------------------------

    onerequest( config) {
        console.log( config)
        return axios.get(BASE_URL + '/user/1/', config);
    },

    tworequest( config) {
        console.log( config)
        return axios.get(BASE_URL + '/user/4/', config);
    },

    sentall: function( config){
        console.log( "=============")
        return Promise.all([ this.onerequest( config), this.tworequest( config)])
    },
// product type ------------------------------------------------------------------
    getProductType( id, config){
        return axios.get(BASE_URL + "/producttype/" + id + "/", config)
    },


// files ------------------------------------------------------------
    getUserFiles( id, type, config){
        return axios.get(BASE_URL + "/api/files/" + id + "/" + type + "/", config)
    },

    patchFile( id, data, config){
        return axios.patch( BASE_URL + "/file/" + id + "/", data, config)
    },
}