import http from '@/api/http'
// import router from '@/router/router';

export default {
    namespaced: true,
    state: {
        name: "profile aaaaaaa",
        profile_status: false,
        id : null,
        name_a: null,
        name_b: null,
        name_c: null,
        phone: null,
        location: null,
        event_date: null,
        extra_info: null,
        slug: null,
        // product information
        product_status: null,
        product_type: null,
        product_items : null,
        product_counts : null,
    },
    mutations: {
        set_profile_status(state, status) {
            state.profile_status = status
        },
        set_profile_profile(state, profile) {
            state.id = profile.id,
            state.name_a = profile.name_a,
            state.name_b = profile.name_b,
            state.name_c = profile.name_c,
            state.phone = profile.phone,
            state.location = profile.location,
            state.event_date = profile.event_date
            state.extra_info = profile.extra_info,
            state.slug = profile.slug,
            state.product_type = profile.product_type
        },
        set_producttype_item( state, item){
            state.product_items = item
        }
    },
    actions: {
        // from user vuex
        async initprofile({ state, commit, rootState, dispatch}, id) {
            if (rootState.user.user_status == true) {
                await http.getProfile(id.id, rootState.user.config)
                    .then((resp) => {
                        commit('set_profile_profile', resp.data)
                        commit('set_profile_status', true)
                    })
                    .catch(() => {
                        commit('set_profile_profile', null)
                        commit('set_profile_status', false)
                    })
                if ( state.product_type != null){
                    http.getProductType( state.product_type, rootState.user.config)
                    .then((resp) => {
                        //console.log( resp.data)
                        //console.log( JSON.parse( resp.data.counts))
                        state.product_items = resp.data.items
                        state.product_counts = JSON.parse( resp.data.counts)
                    })
                }
                dispatch('content/init_files', { 
                                                    id:rootState.user.userid, 
                                                    type:state.product_type,
                                                    config : rootState.user.config
                                                }, {root: true})
                                                            
            }
            else {
                commit('set_profile_profile', null)
                commit('set_profile_status', false)
            }
        }
    },
}
