import http from '@/api/http'

export default {
    namespaced: true,
    state: {
        file_data: [],
        status_modal : false,
        clicked_image : null,
        clicked_id : null,

    },
    mutations: {
        set_content_data(state, data) {
            state.file_data = data
        },
        set_status_modal(state, data){
            state.status_modal = data
        },
        set_clicked_image(state, data){
            state.clicked_image = data
        },
        set_clicked_id(state, data){
            state.clicked_id = data
        }
    },
    actions: {
        async init_files({ commit }, { id, type, config }) {
            await http.getUserFiles(id, type, config)
                .then((resp) => {
                    commit('set_content_data', resp.data)
                    // console.log( JSON.parse( resp.data))
                    // console.log( ( resp.data))
                    })
        },
        async put_files({state}, {id, data, config, index}){
            await http.patchFile( id, data, config)
            .then((resp) =>{
                for(var i = 0 ; i < state.file_data[index].length ; i++){
                    if (state.file_data[index][i].id == resp.data.id){
                        state.file_data[index][i] = resp.data
                    }
                }
            })
            .catch((err) =>{
                console.log(err)
            })
        },
        sort_save( {state, rootState}, {index}){
            
            for(var i = 0 ; i < state.file_data[index].length ; i++){
                var item_id = state.file_data[index][i].id
                var data = {
                    "order" : state.file_data[index][i].order
                }
                // console.log( item_id, data)
                http.patchFile( item_id, data, rootState.user.config)
                .then((resp) =>{
                    console.log( resp)
                })
            }
        }
    }
}
