import http from '@/api/http'
// import router from '@/router/router';

export default {
    /* eslint-disable no-unused-vars */
    namespaced: true,
    state() {
        return {
            name: "useraaaaaaaa",

            user_status: false,
            username: "",
            password: "",
            access_token: null,
            userid: null,
            all_user: null,
            config: null,
        }
    },
    getters:{
        username: state =>{
            return state.username
        }
    },
    mutations: {
        set_access_token(state, access_token) {
            state.access_token = access_token;
        },
        set_user_status(state, user_status) {
            state.user_status = user_status
        },
        set_user_username(state, username) {
            state.username = username
        },
        set_user_userid(state, userid) {
            state.userid = userid
        },
        set_user_config(state, token) {
            state.config = { headers: { Authorization: `Bearer ${token}` } };
        },
    },
    actions: {

        //user_getProfile({state, rootState, dispatch}){
        //   dispatch( 'profile/initprofile', {id: state.userid},  {root: true})
            // console.log(rootState.profile.name)
        // },

        async user_verify({ state, commit, dispatch }) {
            const access_token = localStorage.getItem("access_token");
            const userid = localStorage.getItem("userid");
            const username = localStorage.getItem("username");
          

            await http.verifyToken(access_token)
                .then(() => {
                    commit('set_access_token', access_token)
                    commit('set_user_username', username)
                    commit('set_user_userid', userid)
                    commit('set_user_status', true)
                    commit('set_user_config', access_token)
                    dispatch( 'profile/initprofile', {id: state.userid},  {root: true})
                    // router.push( {name : 'registration'})
                })
                .catch((err) => {
                    commit('set_access_token', null)
                    commit('set_user_username', null)
                    commit('set_user_userid', null)
                    commit('set_user_status', false)
                    commit('set_user_config', null)
                    localStorage.removeItem("access_token");
                    localStorage.removeItem("userid");
                    localStorage.removeItem("username");
                    console.log(err)
                    // router.push( {name : 'login'})
                })

        },
        async user_get({ state, commit, dispatch, rootState}, {id, pw}) {
            await http.postToken(id, pw)
                .then((resp) => {
                    localStorage.setItem("userid", resp.data.id);
                    localStorage.setItem("access_token", resp.data.access);
                    localStorage.setItem("username", resp.data.username);
                    commit('set_access_token', resp.data.access)
                    commit('set_user_username', resp.data.username)
                    commit('set_user_userid', resp.data.id)
                    commit('set_user_status', true)
                    commit('set_user_config', resp.data.access)
                    dispatch( 'profile/initprofile', {id: state.userid},  {root: true})
                })
                // .then(() =>{
                //     if (rootState.profile.product_status == true){
                //         router.push( {name : 'content'})
                //     }
                //     else(
                //     router.push( {name : 'registration'})
                //     )
                // })
                .catch(() => {
                    commit('set_access_token', null)
                    commit('set_user_username', null)
                    commit('set_user_userid', null)
                    commit('set_user_status', false)
                    commit('set_user_config', null)
                    localStorage.removeItem("access_token");
                    localStorage.removeItem("userid");
                    localStorage.removeItem("username");
                })
        },
    }
/* eslint-enable no-unused-vars */
}